.recipe-page-container {
  margin: 30px;
}

.icon-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.icon-button {
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  cursor: pointer;
  background: none;
  border: 2px solid black;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;

}

.icon {
  margin: 0 auto;
  display: block;
}

.recipe-title {
  word-wrap: break-word;
}

.info-container {
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  padding: 30px 20px 10px 20px;
  padding-right: 35px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  gap: 20px;
}

.icon-background.active.no-pointer {
  cursor:auto;
}

.rating-container {
  width: 75px;
  min-width: 75px;
  height: 130px;
}

#rating-container-cook {
  display: flex;
  gap: 4px;
}

.star-rating {
  width: 25px;
  height: 25px;
}

.star-rating.filled {
  fill: var(--yellow);
}

.attributes-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.attribute-text {
  font-size: 16px;
  margin: 0px;
  margin-bottom: 5px;
  color: var(--black);
  font-family: 'Open Sans';
  word-wrap: break-word;
}

.primary-btn.cook-now {
  margin-top: 30px;
}

.category-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  height: 130px;
  overflow-y: hidden;
  overflow-x: auto; /* hide scrollbar when it is not needed */
}

@media only screen and (max-width: 400px) {
  .info-container {
    padding: 25px 5px 3px 0px;
  }
}

@media only screen and (max-width: 550px) {
  .icon-background.active.no-pointer {
    width: 55px;
    height: 55px;
  }
  .category-icon.active.recipe-page {
    width: 36px;
  }
  .category-text.recipe-page {
    font-size: 14px;
  }
  .category-card {
    width: 75px;
  }
  .rating-container {
    width: 65px;
    min-width: 65px;
    padding-top: 15px;
    height: 120px;
  }
  .star-rating {
    width: 20px;
    height: 20px;
  }
  .info-container {
    gap: 10px;
  }
  .category-container {
    height: 120px;
  }

}

@media only screen and (max-width: 1024px) {
  .attribute-container {
    text-align: left;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media only screen and (min-width: 1025px) {
  .recipe-page-container {
    box-sizing: border-box;
    margin: 40px auto;
    width: 55vw;
  }
  .info-container {
    justify-content: center;
    gap: 50px;
  }   
}